

  .post_block {
    display: flex;
    align-items: center;
    padding: 20px 0;
    border-bottom: 1px solid #000;
    &:last-child {
      border: none;
    }


    .avatar {
      width: 50px;
      height: 50px;
      border-radius: 50%;
      background: #000;
      position: relative;
      &:after {
        content: "A";
        color: #fff;
        font-size: 30px;
        position: absolute;
        top: 3px;
        left: 16px;
      }
    }

    .text_post {
      padding-left: 20px;
      font-size: 20px;
      font-family: Arial, sans-serif;
    }
  }













